import React, {useState, useEffect} from 'react';
import {useSelector} from 'react-redux';
import {useLocation, useHistory} from 'react-router-dom';
import Selector from '../../../../common_components/Selector/Selector';
import {
    NO_FILTER_VALUE,
    PER_DAY,
    PER_WEEK,
    PER_MONTH,
    BEGIN_DAY,
    BEGIN_MONTH,
    BEGIN_WEEK,
    CAR_LIST_CATALOG,
    CITY_LIST_CATALOG,
    STATUS_LIST_CATALOG,
    DATE_FROM_FILTER_NAME,
    CAR_FILTER_NAME,
    CITY_FILTER_NAME,
    STATUS_FILTER_NAME,
    PAGE_FILTER_NAME
} from '../../../../settings';
import {
    ADMIN_APP_URL, ORDER_LIST_APP_URL
} from '../../../../urls';
import {getCatalog} from '../../../../store/selectors';
import './OrderFilters.scss';

function OrderFilters() {
    const location = useLocation();
    const history = useHistory();

    let [selectedDate, setSelectedDate] = useState(NO_FILTER_VALUE);
    let [selectedCar, setSelectedCar] = useState(NO_FILTER_VALUE);
    let [selectedCity, setSelectedCity] = useState(NO_FILTER_VALUE);
    let [selectedStatus, setSelectedStatus] = useState(NO_FILTER_VALUE);

    const carList = useSelector(getCatalog(CAR_LIST_CATALOG));
    const cityList = useSelector(getCatalog(CITY_LIST_CATALOG));
    const statusList = useSelector(getCatalog(STATUS_LIST_CATALOG));

    //   ,        URL      ,   
    useEffect(() => {
        const searchParams = new URLSearchParams(location.search);
        const defaultDate = searchParams.get(DATE_FROM_FILTER_NAME);
        const defaultCar = searchParams.get(CAR_FILTER_NAME);
        const defaultCity = searchParams.get(CITY_FILTER_NAME);
        const defaultStatus = searchParams.get(STATUS_FILTER_NAME);

        setSelectedDate(defaultDate ? defaultDate : NO_FILTER_VALUE);
        setSelectedCar(defaultCar ? defaultCar : NO_FILTER_VALUE);
        setSelectedCity(defaultCity ? defaultCity : NO_FILTER_VALUE);
        setSelectedStatus(defaultStatus ? defaultStatus : NO_FILTER_VALUE);
    }, []);

    //     
    const dateSelectorItems = [
        {name: '  ', value: NO_FILTER_VALUE},
        {name: '   ', value: BEGIN_DAY},
        {name: '  ', value: PER_DAY},
        {name: '   ', value: BEGIN_WEEK},
        {name: '   (7 )', value: PER_WEEK},
        {name: '   ', value: BEGIN_MONTH},
        {name: '   (30 )', value: PER_MONTH},
    ];

    const handleDateSelect = value => setSelectedDate(value);

    //      
    const carSelectorItems = [{name: ' ', value: NO_FILTER_VALUE}];
    for (let {id, name} of carList) carSelectorItems.push({name, value: id});

    const handleCarSelect = value => setSelectedCar(value);

    //     
    const citySelectorItems = [{name: ' ', value: NO_FILTER_VALUE}];
    for (let {id, name} of cityList) citySelectorItems.push({name, value: id});

    const handleCitySelect = value => setSelectedCity(value);

    //       
    const statusSelectorItems = [{name: ' ', value: NO_FILTER_VALUE}];
    for (let {id, name} of statusList) statusSelectorItems.push({name, value: id});

    const handleStatusSelect = value => setSelectedStatus(value);

    //   
    const handleApplyFilters = () => {
        const params = new URLSearchParams(location.search);

        //     -    
        params.set(PAGE_FILTER_NAME, '0');

        if (selectedDate === NO_FILTER_VALUE) {
            params.delete(DATE_FROM_FILTER_NAME);
        } else {
            params.set(DATE_FROM_FILTER_NAME, selectedDate);
        }
        if (selectedCar === NO_FILTER_VALUE) {
            params.delete(CAR_FILTER_NAME);
        } else {
            params.set(CAR_FILTER_NAME, selectedCar);
        }
        if (selectedCity === NO_FILTER_VALUE) {
            params.delete(CITY_FILTER_NAME);
        } else {
            params.set(CITY_FILTER_NAME, selectedCity);
        }
        if (selectedStatus === NO_FILTER_VALUE) {
            params.delete(STATUS_FILTER_NAME);
        } else {
            params.set(STATUS_FILTER_NAME, selectedStatus);
        }

        history.push(`/${ADMIN_APP_URL}/${ORDER_LIST_APP_URL}/?${params}`);
    }

    //   
    const handleResetFilters = () => {
        setSelectedDate(NO_FILTER_VALUE);
        setSelectedCar(NO_FILTER_VALUE);
        setSelectedCity(NO_FILTER_VALUE);
        setSelectedStatus(NO_FILTER_VALUE);
        history.push(`/${ADMIN_APP_URL}/${ORDER_LIST_APP_URL}/?${PAGE_FILTER_NAME}=0`);
    }

    return (
        <div className="order_filters">
            <div className="order_filters__selectors_block">
                <Selector items={dateSelectorItems} handleSelect={handleDateSelect} value={selectedDate}/>
                <Selector items={carSelectorItems} handleSelect={handleCarSelect} value={selectedCar}/>
                <Selector items={citySelectorItems} handleSelect={handleCitySelect} value={selectedCity}/>
                <Selector items={statusSelectorItems} handleSelect={handleStatusSelect} value={selectedStatus}/>
            </div>
            <div className="order_filters__control_block">
                <button className="button button_red" onClick={handleResetFilters}></button>
                <button className="button button_blue" onClick={handleApplyFilters}></button>
            </div>
        </div>
    );
}

export default OrderFilters;